import React, { useEffect } from 'react'
import Hero from '@/components/Hero'
import { useScrollSpy, SECTION_IDS } from '@/hooks/useScrollSpy'

const HomePage: React.FC = () => {
  // Ïä§ÌÅ¨Î°§ Ïä§ÌååÏù¥ ÌõÖ ÏÇ¨Ïö© (Í∏∞Î≥∏ ÏòµÏÖò: offset=80px, debounceDelay=100ms)
  useScrollSpy()

  // ÌéòÏù¥ÏßÄ Î°úÎìú Ïãú Ïä§ÌÅ¨Î°§ ÏúÑÏπò Î≥µÏõê Î∞è ÏÑπÏÖò ÌïòÏù¥ÎùºÏù¥Ìä∏
  useEffect(() => {
    // URL Ìï¥ÏãúÍ∞Ä ÏûàÏúºÎ©¥ Ìï¥Îãπ ÏÑπÏÖòÏúºÎ°ú Ïä§ÌÅ¨Î°§
    const hash = window.location.hash.slice(1)
    if (hash && SECTION_IDS.includes(hash as any)) {
      setTimeout(() => {
        const element = document.getElementById(hash)
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' })
        }
      }, 100)
    }
  }, [])

  return (
    <main role="main" aria-label="Ìè¨Ìä∏Ìè¥Î¶¨Ïò§ Î©îÏù∏ ÏΩòÌÖêÏ∏†">
      {/* Hero ÏÑπÏÖò */}
      <section 
        id="hero" 
        aria-labelledby="hero-title"
        className="min-h-screen"
      >
        <Hero />
      </section>

      {/* Ïä§ÌÅ¨Î°§ Ïä§ÌååÏù¥ ÎîîÎ≤ÑÍ∑∏ Ï†ïÎ≥¥ Ï†úÍ±∞Îê® */}
    </main>
  )
}

export default HomePage
